

.left-box {
  font-size: 22px;
  font-family: 'Arial';

  text-align: left;
  background-color: #cf8090;
  padding: 5%;

  flex: 20%;
  margin-left: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgb(47, 43, 43);
  color: black;
  box-sizing: border-box;

  
}
.pic-left-box {
  font-size: 22px;
  text-align: center;
  background-color: #cf8090;
  font-family: 'Arial';
  color: black;
  padding-bottom: 1%;  padding-top: 4%;
  flex: 20%;
  margin-left: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgb(47, 43, 43);
  box-sizing: border-box;

}

.button-design{
  box-shadow: 1px 1px 15px rgb(47, 43, 43);

}
.center-box {
  font-size: 22px;
  text-align: center;
  background-color: #cf8090;
  font-family: 'Arial';
  color: black;
  padding-bottom: 1%;  padding-top: 4%;
  flex: 50%;
  margin-left: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgb(47, 43, 43);
  box-sizing: border-box;

  
}
.right-box {
  font-family: 'Arial';
  font-size: 22px;
  text-align: left;
  background-color: #cf8090;
  padding: 6%;
  box-sizing: border-box;
  flex: 40%;
  margin-left: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgb(47, 43, 43);
  color: black;


}

.top-box {
  font-size: 18px;
  font-family: 'Arial';
  text-align: left;
  background-color: #cf8090;
  padding: 0px;
  flex: 40%;
  box-shadow: 1px 1px 5px rgb(47, 43, 43);
  color: black;
  box-sizing: border-box;

}

@media (max-width: 1200px) {
  .left-box, .right-box  {
    flex: 100%;
    margin-bottom: 10px;
  }
}

@media (max-width: 1200px) {
  .pic-left-box, .right-box  {
    flex: 100%;
    margin-bottom: 10px;
  }
}
@media (max-width: 1200px) {
  .top-box {
    flex: 100%;
  }
}

@media (max-width: 1200px) {
  .center-box  {
    flex: 100%;
  }
}

.flex-box{
  display: flex;
  flex-wrap: wrap;
}

.Moving-name{
  font-family: Arial;
  font-size: 15px;
  color: black;
  position: relative;
  animation: movementname 8s infinite;
  background: #cf8090;
}

@keyframes movementname {
  from {
    left: 0px;
  }
  to {
    left: 25px;
  }
}

.App {
  text-align: left;
}
.App_center {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.bar-top{
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  background-color:#cf8090;
}

.App-header {
  background-color: #a8334a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
}



.Box{
  padding: 15px;
  border-radius: 50px ;
  margin-right: 10px;
  color: black;
}
.Box:hover{
  padding: 15px;
  border-radius: 50px ;
  margin-right: 10px;
  color: rgba(255, 255, 255, 0.731);
}

.a1{
  color: rgb(0, 0, 0);
}
.a1:hover{
  color: rgba(255, 255, 255, 0.731);
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
